    color: rgb(35, 68, 179);
    font-weight: 600;
    background: rgba(17, 133, 112, 0.3);
    box-shadow: inset -3px -3px 6px rgba(255, 255, 255, 0.55), inset 3px 3px 6px rgba(0, 0, 0, 0.55);
    border-radius: 7px;
}

/* extra */
::-webkit-input-placeholder{
    font-weight: 400;
    color: #C2F0E8da;
}
::placeholder{
    font-weight: 400;
    color: #C2F0E8da;
}



