.main {
    height: 100vh;
    position: relative;
    background-color: #ffaf8a;
}

.main > img {
    position: absolute;
    top: 5%;
    left: 4%;
    cursor: pointer;
    width: 200px;
    pointer-events: none;
}

.logoname-container{
    display: flex;
    flex-direction: row;
    padding-left: 70px;
    padding-top: 50px;
    align-items: center;
}

.logoname-container > img{
    height: 80px;
    width: 80px;
}

.logoname-container > span{
    margin-left: 15px;
    font-size: 35px;
    color: rgb(14, 135, 176);
    font-weight: bold;
    font-family: 'Barlow', sans-serif;
}


.main-container{
    /* padding-top: 8rem; */
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.main-description{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    margin: 2rem;
    margin-left: 14rem;
    flex: 0.4 1;
}

.main-description > h1{
    margin-bottom: 20px;
    font-size: 3.25rem;
    width: 90%;
    font-family: 'Barlow', sans-serif; 
    color: #1a1a1a;
    line-height: 111%;
}

.main-description > p{
    font-size: 1.5rem;
    color: #1a1a1a;
    width: 90%;
    font-family: 'Barlow', sans-serif; 
}

.main-description > button{
    margin-top: 2rem;
    font-size: 1.5rem;
    background-size: 300% 100%;
    background-image: linear-gradient(to right, #1090D9, #00B4D7, #041e91);
    box-shadow: 3px 3px 6px #0000006c, 8px 8px 20px #00000030,
    -3px -3px 6px #ffffff90, -8px -8px 20px #ffffff30;
    border-radius: 70px;
    padding: 0.75rem 2rem;
    font-family: Nunito;
    font-style: normal;
    text-transform: inherit;
    font-weight: bold;
    text-align: center;
    color: #303030;

    transition: 0.5s ease;
    
}

.main-description > button:hover{
    background-position: 80% 0;
    transition: all .4s ease-in-out;
    color: #efefef;
}

.img-container {
    flex: 0.6 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.img-container > img {
    width: 550px;
    pointer-events: none;
}





@media (min-width: 992px) and (max-width: 1380px) {
    .main-container{
        padding-top: 5rem;

    }
    .main-description{
        margin-left: 6rem;
    }
    

}


